/* //헤더 */
/* 배너 */
.banner {
  position: relative;
  height: 32.3vw;
  background-image: url("/public/img/banner.jpg");
  background-size: cover;
}
.banner > .slogan {
  position: absolute;
  top: 33.031%;
  left: 10.2%;
