}
.banner > .slogan > img {
  max-width: 100%;
}
.banner > .drink > div {
  position: absolute;
}
.banner > .drink > div > img {
  max-width: 100%;
}
.banner > .drink > .drink1, .banner > .drink > .drink2, .banner > .drink > .drink3 {
  opacity: 0;
  animation: bannerAnim 0.6s ease;
  animation-fill-mode: both;
}
.banner > .drink > .drink1 {
  top: 19.5%;
  left: 51.2%;
  width: 19.5%;
}
.banner > .drink > .drink2 {
  top: 14%;
  left: 40.5%;
  width: 17.5%;
  animation-delay: 0.6s;
}
.banner > .drink > .drink3 {
  top: 19.5%;
  left: 62.6%;
  width: 19.5%;
  animation-delay: 1.2s;
}
.banner > div:last-child {
  position: absolute;
  top: 60%;
  left: 17.5%;
  width: 12%;
  opacity: 0;
  animation: bannerAnim 0.6s ease;
  animation-fill-mode: both;
  animation-delay: 1.8s;
}
.banner > div:last-child > a {
  display: block;
  width: 125px;
  line-height: 34px;
  text-align: center;
  border: 2px solid #f29440;
  border-radius: 5px;
  box-sizing: border-box;
  transition: background-color 0.7s, color 0.7s;
  color: #f29440;
  font-size: 14px;
  margin-inline: auto;
}
.banner > div:last-child > a:hover {
  background-color: #f29440;
  color: white;
  text-decoration: underline;
}
@keyframes bannerAnim {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
